import React, { useMemo, useState } from "react";
import CaterinaPic from "./Caterina_Esposito_Pic.jpeg";

// Skill Descriptions
const skillDescriptions = {
  JavaScript:
    "JavaScript is a versatile, high-level programming language used to create interactive web applications.",
  Java:
    "Java is a robust, object-oriented programming language widely used for building enterprise-level applications.",
  Python:
    "Python is a high-level, interpreted language known for its simplicity and versatility.",
  SQL:
    "SQL is a domain-specific language used to manage and manipulate relational databases.",
  JUnit:
    "JUnit is a testing framework for Java programming language to write and run repeatable automated tests.",
  "Spring Boot":
    "Spring Boot is a framework that simplifies the development of Java-based enterprise applications.",
  Git:
    "Git is a version control system that helps developers track changes and collaborate on code.",
  "REST APIs":
    "REST APIs enable communication between systems using HTTP methods for web services.",
  Node:
    "Node.js is a JavaScript runtime that allows developers to build scalable server-side applications.",
  MongoDB:
    "MongoDB is a NoSQL database that stores data in flexible, JSON-like documents.",
  Bootstrap:
    "Bootstrap is a popular front-end framework for building responsive websites and web applications.",
  React:
    "React is a JavaScript library for building user interfaces, especially for single-page applications.",
  cPanel: "cPanel is a web hosting control panel used for managing hosting servers.",
  Tableau:
    "Tableau is a powerful data visualization tool used in business intelligence and analytics.",
  Salesforce:
    "Salesforce is a customer relationship management (CRM) platform with applications for sales, customer service, and marketing.",
  "Affinity Designer":
    "Affinity Designer is a vector graphics editor for creating precise illustrations and designs.",
  "MS-Office":
    "Microsoft Office is a suite of productivity software, including Word, Excel, and PowerPoint.",
  "Apache Hadoop":
    "Apache Hadoop is a framework that enables the distributed processing of large data sets across clusters of computers.",
  Figma:
    "Figma is a cloud-based design tool for creating user interfaces and collaborative design projects.",
  "Unit Testing":
    "Unit testing involves testing individual components of a software application to ensure they work as intended.",
  SCRUM:
    "SCRUM is an Agile project management methodology that focuses on iterative progress through collaboration and feedback.",
};

const SKILL_GROUPS = [
  {
    title: "Core Stack",
    skills: ["JavaScript", "React", "Python", "SQL"],
  },
  {
    title: "Backend & APIs",
    skills: ["Node", "Spring Boot", "REST APIs", "MongoDB"],
  },
  {
    title: "Tools",
    skills: ["Git", "Figma", "Tableau", "JUnit", "Bootstrap"],
  },
  {
    title: "Methodologies",
    skills: ["Unit Testing", "SCRUM"],
  },
];

const Home = () => {
  const [selectedSkill, setSelectedSkill] = useState(null);

  const selectedDescription = useMemo(() => {
    if (!selectedSkill) return null;
    return skillDescriptions[selectedSkill] ?? "No description available.";
  }, [selectedSkill]);

  const handleSkillClick = (skill) => {
    setSelectedSkill((prev) => (prev === skill ? null : skill));
  };

  return (
    <section>
      {/* HERO */}
      <header id="home" className="hero">
        <div className="hero-inner">
          <div className="hero-photo">
            <img src={CaterinaPic} alt="Caterina Esposito" />
          </div>

          <div className="hero-content">
            <h1 className="hero-title">Software Engineer</h1>
            <p className="hero-subtitle">
              MSc Computer Science graduate building user-focused web applications and
              structured, algorithm-driven systems.
            </p>

            <div className="hero-ctas">
              <a className="btn btn-primary" href="#projects">
                View Projects
              </a>

              {/* Replace with your actual resume path or Google Drive link */}
              <a className="btn btn-secondary" href="/Caterina-Esposito-Resume.pdf" target="_blank" rel="noreferrer">
                Download Resume
              </a>
            </div>

            <p className="hero-tags">
              React • JavaScript • Python • SQL • REST APIs
            </p>
          </div>
        </div>
      </header>

      {/* SKILLS */}
      <section className="dot-background">
        <div className="skills-section">
          <div className="section-header">
            <h2>Skills</h2>
            <p className="section-subtitle">
              Click a skill to view a short description.
            </p>
          </div>

          <div className="skills-grid">
            {SKILL_GROUPS.map((group) => (
              <div className="skills-card" key={group.title}>
                <h3 className="skills-card-title">{group.title}</h3>

                <div className="skills-chips">
                  {group.skills.map((skill) => {
                    const isActive = selectedSkill === skill;
                    return (
                      <button
                        key={skill}
                        type="button"
                        className={`chip ${isActive ? "chip-active" : ""}`}
                        onClick={() => handleSkillClick(skill)}
                        aria-pressed={isActive}
                      >
                        {skill}
                      </button>
                    );
                  })}
                </div>
              </div>
            ))}
          </div>

          {/* DESCRIPTION PANEL */}
          <div className={`skill-detail ${selectedSkill ? "skill-detail-open" : ""}`}>
            {!selectedSkill ? (
              <p className="skill-detail-empty">Select a skill to see details.</p>
            ) : (
              <>
                <div className="skill-detail-header">
                  <h4 className="skill-detail-title">{selectedSkill}</h4>
                  <button
                    type="button"
                    className="skill-detail-close"
                    onClick={() => setSelectedSkill(null)}
                    aria-label="Close skill details"
                  >
                    ✕
                  </button>
                </div>
                <p className="skill-detail-text">{selectedDescription}</p>
              </>
            )}
          </div>
        </div>
      </section>
    </section>
  );
};

export default Home;
